        display: flex;
        flex-direction: column;
        grid-gap: 20px;
        gap: 20px;
        margin-top: 30px;
        border-radius: 10px;
    }
    .form h2 {
        text-align: center;
        font-size: 1.8rem;
        text-shadow: 3px 3px 6px #00000036;
