.container {
    margin: 50px auto;
    /*el auto sirve para que se centre*/
    width: 400px;
    /*Complementa el centrado*/
    padding: 30px;
    background: rgba(0, 0, 0, .5);
    /*agrega transparencia al color deseado con el valor de .5*/
    border-radius: 10px;
    /*agrega esquinas curvas al marco*/
}

.container h2 {
    margin: 0 0 30px;
    padding: 0;
    font-size: 30px;
    color: #4cc9f0;
    text-align: center;
}

.container .element label {
    color: white;
}

.container .element input {
    width: 100%;
    padding: 10px 0;
    color: white;
    background: transparent;
    border: none;
    border-bottom: 1px solid #fff;
    margin-bottom: 30px;
}

.container .element input[type="submit"] {
    /*este solo aplicara a los elementos de tipo submit*/
    background-color: #f72585;
    border: none;
    letter-spacing: 5px;
    /*separa las letras del texto del boton enviar*/
    text-transform: uppercase;
    /*Convierte todo el texto a mayusc*/
    cursor: pointer;
    /*cambia el icono del puntero por una mano al ponerla encima del boton de send*/
    padding: 15px;
    margin-top: 30px;
    border-radius: 10px;
}

.container .element input:hover[type="submit"] {
    background-color: #56d9e2;
}

